@import "../../styles/colors";
@import "../../styles/mixins";

.Promo {
  &__wrapper {
    @include font-regular;
    margin: 32px auto 24px auto;
    max-width: 1200px;
    padding: 8px 32px;

    background: url("../../../public/bg.png") no-repeat right;
    background-size: contain;

    @media screen and (max-width: $ipad) {
      padding: 8px 20px;
    }

    @media screen and (max-width: $iphone) {
      padding: 8px 16px;
    }
  }

  &__way {
    display: flex;
    position: absolute;
    right: 64px;
    top: 16px;
    font-size: 32px;
    font-weight: bold;
    opacity: 0.33;
    color: $active-color;

    &-wrapper {
      position: relative;

    }

    @media screen and (max-width: 1024px) {
      display: none;
    }
  }

  &__description {
    max-width: 390px;
    font-size: 18px;
    color: white;
    backdrop-filter: blur(4px);
    background: rgba(40, 60, 80, 0.85);
    padding: 20px;
    border-radius: 24px;
    margin-left: 16px;
    border: 2px solid rgba(40, 60, 80);

    @media screen and (max-width: $ipad) {
      padding: 16px;
      border-radius: 16px;
      max-width: 380px;
      border: 1px solid rgba(40, 60, 80);
    }

    &._top {
      position: relative;
      z-index: 5;
      background: rgba(12, 16, 20, 0.75);
      border: 2px solid rgba(12, 16, 20);

      @media screen and (max-width: $ipad) {
        border: 1px solid rgba(12, 16, 20);
      }
    }

    &._next {
      max-width: 568px;
      margin-top: -12px;
      margin-left: 0;

      @media screen and (max-width: $ipad) {
        max-width: 560px;
      }
    }
  }

  &__start {
    font-size: 48px;
    margin-top: 24px;
    text-align: center;
    opacity: 0.15;
    @include font-bold;
    color: $active-color;

    @media screen and (max-width: $ipad) {
      font-size: 32px;
    }

    @media screen and (max-width: $iphone) {
      font-size: 24px;
    }
  }
}
